<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、用户名、订单编号"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.virtualCoin">
                <el-option v-for="status in AllcoinList" :key="status.id" :label="status.name"
                           :value="status.id">
                </el-option>
            </el-select>
            <span>状态</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.entrustStatus"
                       placeholder="全部类型">
                <el-option v-for="status in entrustStatus" :key="status.key" :label="status.display_name"
                           :value="status.key">
                </el-option>
            </el-select>
            <span>交易类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.entrustType"
                       placeholder="全部类型">
                <el-option v-for="status in entrustType" :key="status.key" :label="status.display_name"
                           :value="status.key">
                </el-option>
            </el-select>
            <span class="demonstration">创建时间</span>
            <el-date-picker
                    v-model="dateTime"
                    type="datetimerange"
                    placeholder="选择时间范围"
                    range-separator="/"
                    @change="conditionChange">
            </el-date-picker>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
        </div>
        <el-table
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="120">
                <template scope="scope">
                    <el-button
                            @click.native.prevent="detailedInformation(scope.row)"
                            type="text"
                            size="small">
                        详情
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="currencyTrans.page"
                           :page-sizes="[15,30,45,60]" :page-size="currencyTrans.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total"
                           style="position: absolute; right: 10px;">
            </el-pagination>
        </div>
        <el-dialog title="详情信息" :visible.sync="dialogTableVisible" :close-on-click-modal="false">
            <div style="margin: 0 10px 10px 10px">
                <span style="margin: 10px" v-for="item in entrustType"
                      v-if="item.key === temp.entrustType">交易类型：{{item.display_name}}</span>
                <span style="margin: 10px" v-for="item in AllcoinList"
                      v-if="item.id === temp.virtualCoinId">交易对：{{item.name}}</span>
                <span style="margin: 10px" v-for="item in entrustStatus"
                      v-if="item.key === temp.entrustStatus">委托单状态：{{item.display_name}}</span>
            </div>
            <div style="margin: 10px">
                <span style="margin: 10px">单价：{{temp.entrustPrice}}</span>
                <span style="margin: 10px">数量：{{temp.entrustNum}}</span>
                <span style="margin: 10px">成交数量：{{temp.dealNum}}</span>
                <span style="margin: 10px">已成交金额：{{temp.dealAmount}}</span>
            </div>
            <div>
                <el-table :data="gridData" style="margin-bottom: 20px">
                    <el-table-column property="createTime" label="交易时间" width="200"></el-table-column>
                    <el-table-column property="detailsNum" label="成交数量" width="180"></el-table-column>
                    <el-table-column property="detailsPrice" label="成交单价" width="180"></el-table-column>
                    <el-table-column property="detailsFee" label="手续费" width="178"></el-table-column>
                    <el-table-column property="detailsAmount" label="成交金额" width="180"></el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination @size-change="detailPageSize" @current-change="detailPage"
                                   :current-page="detailTrans.page"
                                   :page-sizes="[15,30,45,60]" :page-size="detailTrans.pageSize"
                                   layout="total, sizes, prev, pager, next, jumper" :total="detailsTotal">
                    </el-pagination>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {entrustrans, entrustdetails, coinlist} from '@/api/capital';
    import DCTP from '@/utils/dctp';

    const entrustStatus = [
        {key: 0, display_name: '全部状态'},
        {key: 4, display_name: '用户撤销'},
        {key: 3, display_name: '完全成交'},
        {key: 2, display_name: '部分成交'},
        {key: 1, display_name: '未成交'},
    ];
    const allCoin = {id: '', name: "全部类型"};
    const entrustType = [
        {key: 0, display_name: '全部类型'},
        {key: 1, display_name: '买入'},
        {key: 2, display_name: '卖出'},
    ];
    export default {
        data() {
            return {
                total: 0,
                dateTime: '',
                allCoin,
                AllcoinList: [],
                detailsTotal: 0,
                entrustType,
                gridData: [],
                entrustStatus,
                dialogTableVisible: false,
                tableData: [],
                temp: {
                    entrustPrice: '',
                    virtualCoin: '',
                    entrustNum: '',
                    entrustStatus: '',
                    dealAmount: '',
                    dealNum: '',
                    entrustType: ''
                },
                currencyTrans: {
                    keyword: '',
                    entrustType: 0,
                    entrustStatus: 0,
                    startTime: '',
                    endTime: '',
                    virtualCoin: '',
                    page: 1,
                    pageSize: 15
                },
                detailTrans: {
                    entrustTransSeq: '',
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [
                    {
                        label: "FID",
                        prop: "entrustTransSeq",
                        align: 'center',
                        width: 55
                    }, {
                        label: "用户UID",
                        prop: "userId",
                        align: 'center',
                        width: 55
                    }, {
                        label: "登录名",
                        prop: "signtype",
                        width: 120,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.phoneNum : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "挂单类型",
                        prop: "virtualCoinName",
                        align: 'center',
                        width: 60,
                    }, {
                        label: "交易类型",
                        prop: "entrustType",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '买入' : (value === 2 ? '卖出' : '未知');
                        }
                    }, {
                        label: "状态",
                        prop: "entrustStatus",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(entrustStatus, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "挂单总额",
                        prop: "entrustAmount",
                        align: 'left',
                        width: 100
                    }, {
                        label: "交易单价",
                        prop: "entrustPrice",
                        align: 'left',
                        width: 80
                    }, {
                        label: "挂单数量",
                        prop: "entrustNum",
                        align: 'left',
                        width: 80
                    }, {
                        label: "未成交数量",
                        prop: "unsettledNum",
                        align: 'left',
                        width: 80
                    }, {
                        label: "已成交数量",
                        prop: "dealNum",
                        align: 'left',
                        width: 80
                    }, {
                        label: "手续费(总)",
                        prop: "fee",
                        align: 'left',
                        width: 80
                    }, {
                        label: "成交总金额",
                        prop: "dealAmount",
                        align: 'left',
                        minWidth: 80
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后更新时间",
                        prop: "updateTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,

        methods: {
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            }
            ,
            conditionQuery() {
                this.currencyTrans.page = 1;
                this.getList();
            }
            ,
            //获取当前页数
            detailPage(val) {
                this.detailTrans.page = val;
                this.getDetailsList();
            }
            ,
            //获取每页多少条
            detailPageSize(val) {
                this.detailTrans.pageSize = val;
                this.getDetailsList();
            }
            ,
            //查询所有列表以及条件查询
            getList() {
                console.log(this.currencyTrans);
                entrustrans(this.currencyTrans).then(response => {
                    this.total = response.data.total;
                    this.tableData = response.data.list;
                    if (this.total <= 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            }
            ,
            conditionChange(val) {
                console.log(val);
                let times = val.split('/');
                this.currencyTrans.startTime = times[0];
                this.currencyTrans.endTime = times[1];
            }
            ,
            detailedInformation(val) {
                this.temp = val;
                this.detailTrans.page = 1;
                this.detailTrans.entrustTransSeq = val.entrustTransSeq;
                console.log(this.detailTrans);
                this.dialogTableVisible = true;
                this.getDetailsList();
            }
            ,
            getDetailsList() {
                entrustdetails(this.detailTrans).then(response => {
                    this.gridData = response.data.list;
                    this.detailsTotal = response.data.total;
                    if (this.detailsTotal <= 0) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            },
            getCoinList() {
                coinlist().then(response => {
                    this.AllcoinList = response.data;
                    this.AllcoinList.unshift(this.allCoin);
                })
            }
        }
        ,
        created() {
            this.getCoinList();
            this.getList();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .span {
        width: 300px;
        margin: 10px;
    }
</style>



